          cursor: pointer;
          padding: 0;
          border: 0;
          background: none;
          vertical-align: baseline;
          font-family: inherit;
          font-weight: inherit;
          -webkit-appearance: none;
          appearance: none;
          -webkit-font-smoothing: antialiased;
