          .notepad .list ul li .remove:after {
            content: 'Ã—'; }
          @media (hover: hover) and (pointer: fine) {
            .notepad .list ul li .remove {
              display: none; } }
        .notepad .list ul li:hover .remove {
          display: block; }
        .notepad .list ul li .todo-text {
